.theme-toggler{
    display: flex;
    align-items: center;
}

.checkbox-theme {
    opacity: 0;
    position: absolute;
  }

.checkbox-label {
    box-sizing: border-box;
    background-color: #111;
    width: 50px;
    height: 26px;
    border-radius: 50px;
    position: relative;
    padding: 5px;
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .fa-moon {color: #f1c40f;}
  
  .fa-sun {color: #f39c12;}
  
  .checkbox-label .ball {
    background-color: #fff;
    width: 22px;
    height: 22px;
    position: absolute;
    left: 2px;
    top: 2px;
    border-radius: 50%;
    transition: transform 0.2s linear;
  }
  
  .checkbox-theme:checked + .checkbox-label .ball {
    transform: translateX(24px);
  }
  
  
  
  
  
  